.breed-select {
  width: 300px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  overflow: hidden;
}

.breed-select__select {
  width: 100%;
  background-color: #f1f1f1;
  border: none;
  padding: 10px;
  transition: all .3s;
}

.breed-select__select:hover {
  background-color: #e0e0e0;
}

.loader {
  perspective: 1000px;
  width: 48px;
  height: 48px;
  color: #12148e;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotateX(70deg);
}

.loader:after {
  color: #d00303;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

@keyframes borderAnimation {
  0% {
    border-color: rgba(30, 0, 255, .8);
  }

  50% {
    border-color: rgba(56, 90, 184, .8);
  }

  100% {
    border-color: rgba(16, 192, 165, .8);
  }
}

@keyframes textAnimation {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

